import React from 'react'
import './education.css'
import Data from '../Asset/Data/data'
export default class education extends React.Component{
    state={
        Data:Data
    }
    render() {
        let res = this.state.Data.map((obj,i)=>{
            return <div>
                <h5 className="edu_title">{obj.education.edu_title}</h5>
                <h5>{obj.education.uni.degree_title}</h5>
                <h5 style={{textTransform:"capitalize",color:"black"}}><a href="https://www.lgu.edu.pk/" target="_blank">{obj.education.uni.uni_name}</a></h5>
                <p style={{color:"red"}}>{obj.education.uni.start_date}-{obj.education.uni.end_date}</p>
                <p className="project_det">{obj.education.uni.project}</p>
                <p className="project_url"><a href="http://imperialdelivery.com.pk/" target="_blank" rel="noopener noreferrer">{obj.education.uni.project_url}</a></p>
            </div>
        })
        return (
            <div className="container">
                {res}
            </div>
        )
    }
}