import React from 'react'
import './language.css'
import Data from '../Asset/Data/data'
export default class language extends React.Component{
    state={
        Data:Data
    }
    render() {
        let res = this.state.Data.map((obj,i)=>{
            return <div>
                <h5 style={{fontWeight:"bold"}}>Language</h5>
                <div className="main_lang">
                    <div className="lang_div">
                    <span className="Urdu_clip">
                  </span>
                  <span className="lan">
                        Urdu
                    </span>
                    </div>
                    <div className="lang_div">
                    <span className="Urdu_eng">
                        <span className="eng_second">

                        </span>
                  </span>
                  <span className="lan">
                        English
                    </span>
                    </div>
                    <div className="lang_div">
                    <span className="Urdu_clip">
                  </span>
                  <span className="lan">
                        Punjabi
                    </span>
                    </div>
                </div>
            </div>
        })
        return (
            <div className="container"> {res}</div>
        )
    }
}