import React from 'react'
import './certifications.css'
import Data from '../Asset/Data/data'

export default class certification extends React.Component{
    state={
        Data:Data
    }
    render() {
        let res = this.state.Data.map((obj,i)=>{
            return <div>
                <h5 className="certi_title">{obj.certfification.certi_title}</h5>
                <div className="certi_main row">
                    <span className="col-md-6">{obj.certfification.certi_detail[0]}</span>
                    <span className="col-md-6">{obj.certfification.certi_detail[1]}</span>
                </div>
                <div className="certi_main">
                    <span className="certi_left">{obj.certfification.certi_detail[2]}</span>
                    
                </div>
            </div>
        })
        return (
            <div className="container">
                {res}
            </div>
        )
    }
}